<template>
  <div class="problemInfo">
    <div class="goback">
      <div @click="goback"><i class="el-icon-arrow-left"></i> {{info.title}}</div>
    </div>
    <div class="container">
      <div class="problemTitle">
        <div class="icon">
          <div>？</div>
        </div>
        <div class="info">
          <div>
            <div class="title">{{info.title}}</div>
          </div>
          <div class="date">{{info.date}}</div>
        </div>
      </div>
      <div class="content" v-html="info.content"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'problemInfo',
  data() {
    return {
      info: {}
    }
  },
  created() {
    this.info = JSON.parse(this.$route.query.info)
  },
  methods: {
    goback() {
      this.$router.push({
        name: 'problem'
      })
    }
  }
}
</script>

<style lang="scss">
.problemInfo {
  .goback {
    font-size: 20px;
    padding: 20px 0;
    cursor: pointer;
    letter-spacing: 0.1em;
  }
  .container {
    background-color: #fff;
    border-radius: 10px;
    .problemTitle {
      overflow: hidden;
      width: 100%;
      cursor: pointer;
      .icon {
        float: left;
        padding: 25px 20px 0 20px;
        div {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background-color: #e5f7f3;
          color: #03b187;
          text-align: center;
          line-height: 40px;
          font-size: 20px;
        }
      }
      .info {
        float: left;
        padding: 20px 0;
        margin-right: 20px;
        width: calc(100% - 100px);
        border-bottom: 1px solid #f4f4f4;
        >div {
          overflow: hidden;
          &:first-of-type {
            margin-bottom: 10px;
            .title {
              font-size: 18px;
              float: left;
              color: #333;
              font-weight: 700;
            }
          }
        }
        .date {
          font-size: 12px;
          letter-spacing: 0.1em;
          color: #999;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
    .content {
      padding: 10px 20px 50px 80px;
      letter-spacing: 0.1em;
      line-height: 2;
      font-size: 12px;
      color: #333;
      p {
        text-indent: 2em;
      }
    }
  }
}
</style>


